import MUIDataTable from "mui-datatables";
import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import { columns, data, errorCols, errorOptions } from "./data_header_table";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import { Formik, Form, ErrorMessage } from 'formik'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import * as Yup from 'yup'
import Swal from 'sweetalert2'
import { Grid, TextField } from '@material-ui/core';
import { Autocomplete } from "@material-ui/lab"
import { requesicoes } from "../../../utils/services";
import Input from '../../../ui-component/shared/Input';
import { Fab } from "@material-ui/core";


let paises = [];
let provincias = [];
export default function MuiDatatableExample() {
  const getMuiTheme = () =>
    createMuiTheme({
      overrides: {
        MUIDataTableHeadCell: {
          fixedHeader: { backgroundColor: "darkblue", color: "white" }
        },
        MUIDataTableSelectCell: {
          headerCell: { backgroundColor: "darkblue" },
          checkboxRoot: { color: "white", filter: "brightness(0.5)" },
          expandDisabled: {
            // Soft hide the button.
            visibility: "hidden"
          }
        }
      }
    });

  const [isFormValid, setIsFormValid] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [openProvincia, setOpenProvincia] = React.useState(false);
  const [updateDom, setUpdateDom] = useState(false);

  useEffect(() => {
    init();
  }, [])

  async function init() {

    const [listapaises,listaProvincias] = await Promise.all([requesicoes().buscarPaises(),
      requesicoes().buscarProvincia()]);
    paises = listapaises.data
   provincias=listaProvincias.data.map((p,i)=>({...p,NO:i+1,name:p.name,pais:p.country_id.name}))//
    setUpdateDom(!updateDom);
  }

  const handleClickOpenProvincia = () => {
    setOpenProvincia(true);
  };

  const handleClose = () => {
    setOpenProvincia(false);
  };

  const options = {
    // filterType: "checkbox",
    responsive: "standard",
    elevation: 10,
    print: true,
    selectToolbarPlacement: "true",
    selectableRowsOnClick: true,
    expandableRows: false,
    expandableRowsHeader: false,
    isRowExpandable: (dataIndex, expandedRows) => {
      const { errors } = provincias[dataIndex];
      return errors !== null && errors !== undefined;
    },
    onRowExpansionChange: (curExpanded, allExpanded, rowsExpanded) =>
      console.log(curExpanded, allExpanded, rowsExpanded),
    renderExpandableRow: (rowData, rowMeta) => {

      return (
        <div
          style={{
            marginLeft: "90%",
            width: "100%",
            marginTop: "3%",
            marginBottom: "3%"
          }}
        >
          <MUIDataTable
            columns={errorCols}
            provincias={provincias[5]}
            options={errorOptions}
          />
        </div>
      );
    },
    customToolbarSelect: (selectedRows, displayData, setSelectedRows) => {
      console.log(selectedRows, displayData);
      return <div> am youre toolbar header </div>;
    },
    nextPath(path) {
      this.props.history.push(path);
    },
    customToolbar: () => {

      return (
        <Fab   onClick={handleClickOpenProvincia} variant="extended" size="medium"
        style={{
            position: 'fixed',
            bottom: '60px', zIndex: 500,
            right: '15px'
        }} color="primary">Adicionar</Fab>
                  
      );
    },
    onRowSelectionChange: (
      currentRowsSelected,
      allRowsSelected,
      rowsSelected
    ) => {
      if (
        allRowsSelected !== null &&
        allRowsSelected !== undefined &&
        allRowsSelected.length > 0
      ) {
        setIsFormValid(true);
      } else {
        setIsFormValid(false);
      }
      console.log(currentRowsSelected, allRowsSelected, rowsSelected);
      rowsSelected.map((item) => console.log(provincias[item]));
    }
  };
  const validationSchema = Yup.object().shape({
    name: Yup.string().min(3, "Deve ter no minio 3 caratere").required("Campo Obrigatorio"),
  })
  return (
    <div>
      <MuiThemeProvider theme={getMuiTheme()}>
        <MUIDataTable
          title={'Listagem de Províncias'}
          columns={columns(provincias)}
          data={provincias}
          options={options}
        // components={components}
        />
      </MuiThemeProvider>


    {/* Dialogo provincia */}

      <Dialog open={openProvincia} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Cadastrar Provincia</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Preencha os dados da provincia que pretende cadastrar
          </DialogContentText>
          <Formik initialValues={{}} validationSchema={validationSchema}

            onSubmit={(values, { setSubmitting }) => {
              async function save(values) {

                try {

                  await Promise.resolve(requesicoes().postProvincia(values));
                  //setOpen(true)
                  Swal.fire(
                    'Exito!',
                    'Provincia cadastrada com sucesso!',
                    'success'
                  )
                  init();

                } catch (error) {
                  Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: 'Ocorreu um erro ao cadastrar Curso!!',
                    footer: 'Se o erro pressistir porfavor contacte o centro Informatico'
                  })

                }
              }
              save(values);
              setTimeout(() => {
                setSubmitting(false);
              }, 500);
            }}>
            {(props) => {
              const { isSubmitting, setFieldValue, values } = props;
              return (
                <Form>
                  <Grid container spacing={2}>
                    <Grid item xs={12} sm={5}>
                      <Input name="name"
                        label="Nome"
                        placeholder="Nome da provincia"
                        helperText={<ErrorMessage name='name' />}
                        error={props.errors.name && props.touched.name} />

                    </Grid>
                    <Grid item xs={12} sm={2}>
                      <Input name="sigla"
                        label="Sigla"
                        placeholder="Nome da provincia"
                        helperText={<ErrorMessage name='sigla' />}
                        error={props.errors.sigla && props.touched.sigla} />

                    </Grid>

                    <Grid item xs={12} sm={4} className="AppInput">
                      <Autocomplete name="country_id"
                        onChange={(e, v) => {
                          setFieldValue('country_id', v);
                          }}
                        fullWidth
                        getOptionLabel={option => option.name}
                        options={paises} style={{ minWidth: 200 }}
                        renderInput={(params) => <TextField variant="outlined" margin="dense" label="Pais"
                          fullWidth {...params} placeholder="Seleccione uma opção" />}
                      />

                    </Grid>

                  </Grid>
                  <DialogActions>
                    <Button onClick={handleClose}>Cancelar</Button>
                    <Button type="submit" onClick={handleClose}>Salvar</Button>
                  </DialogActions>
                </Form>)
            }}
          </Formik>
        </DialogContent>
        </Dialog>


    </div>

  );
}
